define(["require", "exports", "TFS/WorkItemTracking/RestClient", "TFS/WorkItemTracking/Contracts"], function (require, exports, WITClient, Models) {
    "use strict";
    Object.defineProperty(exports, "__esModule", { value: true });
    var extensionContext = VSS.getExtensionContext();
    var client = WITClient.getClient();
    var workitemstats = {
        getMenuItems: function (context) {
            var wids = context.workItemIds;
            // Uses the plural of cards if more than one are selected
            // let menuItemText = "One Selected";
            // if (wids && wids.length > 1) {
            //   menuItemText = "Multiple Selected";
            // }
            // console.log(wids.length + "selected");
            // console.log(context);
            // console.log(wids);
            // getWorkItems(wids).then(workItems => workItems.reduce((sum, current) => sum + current.fields["System.StoryPoints"], 0));
            // menuItemText = sum;
            return getWorkItems(wids).then(function (workItems) {
                var workItemSum = 0;
                workItemSum = workItems.reduce(function (sum, current) { return sum + current.fields["Microsoft.VSTS.Scheduling.StoryPoints"]; }, 0);
                var menuItemText = workItemSum + " story points";
                if (workItemSum === 1) {
                    menuItemText = workItemSum + " story point";
                }
                // Add if here to not return a menu item if we have no points?
                return [
                    {
                        // action: // This has no action right now
                        icon: "img/icon.png",
                        text: menuItemText,
                        // title: menuItemText,
                        disabled: true // Disabling this button until I decide to make it do something. It could show an popup with additional stats
                    }
                ];
            });
        }
    };
    // Promises
    function getWorkItems(wids) {
        return client.getWorkItems(wids, undefined, undefined, Models.WorkItemExpand.All);
    }
    VSS.register(extensionContext.publisherId + "." + extensionContext.extensionId + ".workitemstats", workitemstats);
});
